@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}
body{
  font-family: 'Courier New', Courier, monospace;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.right-sidebar{
    position: fixed;
    left:50px;
    font-weight: bold;
    top: 0;
    bottom: 0;
    background-color: transparent;
}
.right-sidebar span img{
    height: 30px;
    position: absolute;
    left: 0;
}

span{
    margin: 20px;
    position: relative;
    color: black;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 200px;
    padding:20px;
    border-radius: 50px;
}
span:hover{
    background-color: rgba(29, 160, 242, 0.349)
}

span *{
    margin: 10px;
}

/* #logo-span{
    align-items: center;
    justify-content: flex-start;
} */
#logo-span:hover{
    background-color: transparent;
}

#logo-span p{
    font-size: 1px
}

span p{
    position: absolute;
    left: 60px;
}

#logo-span img{
    height: 50px;
}
@media only screen and (max-width: 800px){
	span p{
        display: none;
    }
    span img{
        left: 0;
        right: 0;
    }
    span{
        margin: 10px;
        padding: 20px;
        align-items: center;
        justify-content: center;
        width: 10px;
        height: 10px;
        border-radius: 50px;
    }
    .right-sidebar{
        left: 0;
    }
    #logo-span img{
        height: 30px;
    }
    #tweet-button{
        display: none;
    }
}


#tweet-button2{
    margin-left: auto;
    margin-right: auto;
    display: flex;
    border: 0;
    outline: 0;
    background-color: #3aa9ee;
    width: 200px;
    text-align: center;
    align-items: center;
    justify-content: center;
    border-radius: 50px;
    color: white;
    font-weight: bold;
    padding: 10px;
}
body{
    font-family: 'Inter', sans-serif;
}
#top-center{
    display: flex;
    margin-left: auto;
    margin-right: auto;
    align-items: center;
    justify-content: space-between;
    background-color: transparent;
    border: 1px solid rgb(187, 184, 184);
    width: 100%;
    padding: 10px;
    height: 30px;
    border-bottom: 0;
}
.first{
    width: 100%;
    padding: 8px;
    display: flex;  
    align-items: center;
    justify-content: flex-start;
}
.first *{
    margin: 10px;
}
.first input{
    border: 0;
    outline: none;
    font-size: 15px;
}
#center-center{
    /* padding: 10px; */
    width: 103%;
    border: 1px solid rgb(187, 184, 184) ;
}
.center{
    width: 50%;
    align-items: center;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    /* border: 1px solid rgb(187, 184, 184) ; */
}

#image-url{
    width: 90%;
    margin-left: auto;
    margin-right: auto;
    outline: none;
    border: 0;
    font-size: 15px;
    margin: 10px;
}

#tweet-button{
    margin-left: auto;
    margin-right: 10px;
    display: flex;
    border: 0;
    outline: 0;
    background-color: #3aa9ee;
    width: 100px;
    text-align: center;
    align-items: center;
    justify-content: center;
    border-radius: 50px;
    color: white;
    font-weight: bold;
    padding: 10px;
}

#tweet-button:hover{
    background-color: #109cf3;
}

hr{
    height: 10px;
    background-color: rgb(216, 213, 213);
    width: 100%;
    border: 0;
}

.loader{
    margin-left: auto;
    margin-right: auto;
    /* color: #109cf3; */
    height: 100px;
    width: 100px;

}

.posts{
    /* margin: 10px; */
}
main{
    display: flex;
}

main *{
    margin: 10px;
}
.tweet{
    /* margin-bottom: 20px; */
    width: 100%;
    border-bottom: 1px solid rgb(187, 184, 184);
    border-top: 0;

}
.icons{
    margin: 10px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    width: 100%;
}

#image{
    width: 300px;
    height: auto;
    margin-left: auto;
    margin-right: auto;
    border-radius: 20px;
    display: flex;
}
.login{
    display: flex;
    height: 100vh;
   
}

#first{
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: url(https://abs.twimg.com/sticky/illustrations/lohp_en_1302x955.png);
    height: 100%;
    background-color: red;
}

#second div img{
    height: 30px;
}

#second{
    display: flex;
    align-items: center;
    justify-content: center;
}

#second div button{
    padding: 10px;
    border-radius: 50px;
    font-size: 15px;
    color: white;
    font-weight: bold;
    background-color: #3aa9ee;
    border: 0;
    outline: none;
    transition: all 200ms ease-in   ;
    width: 300px;
    
}
h1{
    font-size: 50px;
}
#second div *{
    margin: 50px;
}
#second div button:hover{
    background-color:#1288d1 ;
}

#second{
    background-color: transparent;
    width: 50%;
}
@media only screen and (max-width: 800px){
    .login{
        display: block;
    }
    #first{
        height: 50%;
        width: 100%;
    }
    #second{
        width: 100%;
        height: 50%;
    }
}
.left-sidebar{
    position: fixed;
    right: 0;
    top: 0;
    bottom: 0;
}

body::-webkit-scrollbar{
    background-color: transparent;
    width: 10px;
    padding: 10px;
}
body::-webkit-scrollbar-thumb{
    border-radius: 50px;
    height: 10px;
    background-color: rgb(199, 198, 198);
}

body::-webkit-scrollbar-thumb:hover{
    background-color: rgb(153, 152, 152);
}
